import React, { useContext } from 'react'
import { StateContext } from '../../App';

export function Table() {
    
    let { state, dispatch } = useContext(StateContext)

    const addAmountInputHandler = (e, i) => {
        dispatch({ type: 'ADD_AMOUNT_INPUT', value: e.target.value, i: i })
        dispatch({ type: 'CALCULATE_TABLE_DATA'}) 
    }
    const table = state.tableData.map((el, i) => {
        return (
            <tr key={el + i}>
            <th scope="row">{el.month}</th>
            <td>{el.profitPerMonth}</td>
            <td>{el.balance}</td>
            <td><input type="number" value={el.addAmount == 0 ? '' : el.addAmount} onChange={(e) => (addAmountInputHandler(e, i))} /></td>
          </tr>
        )
      })
    return <>
    {table}
    </>
}


export default Table;
