

function calculateTableData(state, tableData, ...rest) {

     /* HANDLE REST IF WE GOT IT */

    let restObject = {};
    rest.forEach(el => {
        if (el.type in state) {
        restObject = {...restObject,
            [el.type] : el.value
        }
    }
    })



    let arr = tableData || state.tableData;
    let rate = (+state.percentergPerYear.replace(',', '.') / 100) / 12
    let depo = state.deposit;

    arr = arr.reduce((acc, cur, i, arr) => {

        let el;
        let prevBalance;

        switch (i) {
            case 0: el = { month: i + 1, profitPerMonth: +(depo * rate).toFixed(0), balance: +depo + +(depo * rate).toFixed(0) + +tableData[i].addAmount, addAmount: tableData[i].addAmount }
                break;
            default:
                prevBalance = acc[i - 1].balance;
                el = { month: i + 1, profitPerMonth: (prevBalance * rate).toFixed(0), balance: prevBalance + +(prevBalance * rate).toFixed(0) + +tableData[i].addAmount, addAmount: tableData[i].addAmount }
        }
        acc.push(el)
        return acc;
    }, [])


    let avgPerMonth = arr.reduce((acc, cur, i, arr) => {
        return acc += +cur.profitPerMonth / arr.length;
    }, 0)

    let totalProfit = arr.reduce((acc, cur) => {
        return acc += +cur.profitPerMonth;
    }, 0)
    let avgPerYear = (+totalProfit / arr.length) * 12

    let chartNewData = Object.assign({},state.chartData);
    state.tableData.forEach(el => {
        chartNewData.month.push(el.month)
        chartNewData.profit.push(el.profitPerMonth)
        chartNewData.balance.push(el.balance)
        chartNewData.labels.push(el.labels)
      })

    return {
        ...state,
        ...restObject,
        tableData: arr,
        chartData: chartNewData,
        avgPerMonth: avgPerMonth.toFixed(0),
        totalProfit: totalProfit,
        avgPerYear: avgPerYear.toFixed(0),
    }
}


export default function (state, action) {

    switch (action.type) {

        case 'ADD_AMOUNT_INPUT':

            let { value, i } = action;
            let newElement = Object.assign({}, state.tableData[i]);
            newElement.addAmount = value;
            let newArr = [...state.tableData];
            newArr[i] = newElement;

            return calculateTableData(state, newArr)

        case 'START_DEPOSIT_INPUT_VALUE':
            return {
                ...state,
                deposit: action.deposit,
            }

        case 'ADD_MONTH_TO_CALCULATE':
            return {
                ...state,
                monthToCalculate: action.monthToCalculate,
            }

        case 'FILL_ADDAMOUNT_DATA':

            let amountArr = [...state.tableData]
                .map(el => {
                    el.addAmount = action.value;
                    return el;
                })
            return  calculateTableData(state, amountArr, {type:'addAmountToFill', value: action.addAmountToFill})


        case 'LOAD_LOCAL_STORAGE_STATE':
            return {
                ...state,
                avgPerMonth: action.avgPerMonth,
            }
        case 'PERCENTAGE_PER_YEAR_INPUT':
            return {
                ...state,
                percentergPerYear: action.percentergPerYear
            }
        
        case 'FILL_TABLE_DATA':

            let { num } = action;

            let length = state.tableData.length;
            let arr;
            if (length == 0) {
                arr = new Array(+num).fill({ month: '', profitPerMonth: '', balance: '', addAmount: '' })
            }
            else if (num > length) {
                arr = new Array(num - length).fill({ month: '', profitPerMonth: '', balance: '', addAmount: state.addAmountToFill })
                arr = [...state.tableData, ...arr]
            }
            else if (num < length) {
                arr = [...state.tableData]
                arr.length = arr.length - (arr.length - num);
            }
            else return { ...state };

            return calculateTableData(state, arr)

        default: return state;
    }

}