import React from 'react'
import { Header } from './Header'
import { motion } from 'framer-motion';


 function NotFound404(props) {
    

    const initial = {y: 0 , opacity: 0 };
    const animate = {y: 100, opacity: 1};
    const exit = { y:0, opacity: 0 };
    const duration = time => time*0.2;

    return (
        <>
        <Header />
        <motion.div
            initial={{...initial, delay: duration(5)}}
            animate={{...animate, transition: {duration: duration(5)}}}
            exit={{...exit, transition: {duration: duration(5)}}}
            style={{ height: 300, width: 300, display: 'flex', justifyContent: 'center', alignItems: 'center', background: '#ddd', margin: '0 auto' }}>
            <h1>Not Found 404!</h1>
        </motion.div>
        </>
    )
}
export default NotFound404