
import React, { useContext } from 'react'
import { Chart as ChartJS } from 'chart.js/auto'
import { Chart, Line } from 'react-chartjs-2'
import { StateContext } from '../../App';


export function ChartLines(props) {

    let { state, dispatch } = useContext(StateContext)


    let chartData = {
        month: [],
        profit: [],
        balance: [],
        labels: [],
      };
      state.tableData.forEach(el => {
        chartData.month = [...chartData.month, el.month]
        chartData.profit = [...chartData.profit, el.profitPerMonth]
        chartData.balance = [...chartData.balance, el.balance]
        chartData.labels = [...chartData.labels, el.month]
      })

    return (
        <>
            <Line 
data = {{
labels: chartData.month,
datasets: [{
  label: 'Balance',
  data: chartData.balance
},
{
  label: 'Profit',
  data: chartData.profit
}]
}} 
/>
        </>
    )
}
