import React, { Children } from 'react'
import { Header } from './Header'
import { motion } from 'framer-motion';



function StartPage({children}) {
    console.log(children);
    const initial = { y: 0, opacity: 0 };
    const animate = { y: 100, opacity: 1 };
    const exit = { y: 0, opacity: 0 };
    const duration = time => time * 0.2;
    return (<>
        <Header />
        {children('Vasya')}
        <motion.div
            initial={{ ...initial }}
            animate={{ ...animate, transition: { duration: duration(4), type: 'spring' } }}
            exit={{ ...exit, transition: { duration: duration(4), type: 'spring' } }}
            style={{ height: 300, width: 300, display: 'flex', justifyContent: 'center', alignItems: 'center', background: '#ddd', margin: '0 auto' }}>
            <h1>Hello world!</h1>
        </motion.div>

        <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1, transition: { delay: 0.2, duration: 0.7 } }}
            exit={{ opacity: 0, transition: { duration: 0.7 } }}
            style={{ height: 300, width: 300, display: 'flex', justifyContent: 'center', alignItems: 'center', background: '#ddd', margin: '0 auto' }}>
            <h1>Hello world!</h1>
        </motion.div>
       
    </>
    )
}
export default StartPage;