import React, { useEffect, useReducer } from "react";
import './Profile.css';
import reducer from "../../Reducer/reducer";
import { Header } from "../../Header";
import { motion } from 'framer-motion';


const Profile = () => {

    const [state, dispatch] = useReducer(reducer, {
        fname: 'loading',
        lname: 'loading',
        email: 'loading',
    })


    function fetchData() {
        fetch(`https://reqres.in/api/users/${Math.floor((Math.random() * (13 - 1)) + 1)}`)
            .then(res => res.json())
            .then(res => {
                dispatch({
                    type: 'RELOAD',
                    id: res.data.id,
                    avatar: res.data.avatar,
                    fname: res.data.first_name,
                    lname: res.data.last_name,
                    email: res.data.email,
                })
            })
    }


    useEffect(() => {
        fetchData();
        return () => {
            console.log(`Closed`);
        }
    }, [])


    const initial = { y: 0, opacity: 0 };
    const animate = { y: 100, opacity: 1 };
    const exit = { y: 0, opacity: 0 };
    const duration = time => time * 0.2;


    return <>
        <Header />
        <motion.div
            initial={{ ...initial }}
            animate={{ ...animate, transition: { duration: duration(2) } }}
            exit={{ ...exit, transition: { duration: duration(2) } }}
            className="container mt-5">

            <div className="row d-flex justify-content-center">

                <div className="col-md-7">

                    <div className="card p-3 py-4">

                        <div className="text-center">
                            <img src={state.avatar} width="100" className="rounded-circle" />
                        </div>

                        <div className="text-center mt-3">
                            <h5 className="mt-2 mb-0">{`${state.fname} ${state.lname}`}</h5>
                            <span>{state.email}</span>

                            <div className="px-4 mt-1">
                                <p className="fonts">Lorem ipsum dolor sit amet consectetur adipisicing elit. Earum, cumque. Earum qui enim, dolore animi tempora in necessitatibus sint iusto reiciendis fugiat ducimus atque impedit unde ipsum voluptatem id vero!</p>

                            </div>

                            <ul className="social-list">
                                <li><i className="fa fa-facebook"></i></li>
                                <li><i className="fa fa-dribbble"></i></li>
                                <li><i className="fa fa-instagram"></i></li>
                                <li><i className="fa fa-linkedin"></i></li>
                                <li><i className="fa fa-google"></i></li>
                            </ul>

                            <div className="buttons">

                                <button className="btn btn-outline-primary px-4" onClick={() => fetchData()}>Reload</button>

                            </div>


                        </div>




                    </div>

                </div>

            </div>

        </motion.div>
    </>
}

export default Profile;