import React from 'react'
import { NavLink } from 'react-router-dom';

export function Header(props) {


    return (
        <>
            <div>
                <NavLink to="/">Start Page</NavLink>
            </div>
            <div>
                <NavLink to="/profile">Profile</NavLink>
            </div>
            <div>
                <NavLink to="/percents">Percents</NavLink>
            </div>
        </>
    )
}
