
import './App.css';
import React, { useReducer } from 'react';
import PercentReducer from './components/Percents/PercentReducer/PercentReducer';
import { HashRouter as Router } from 'react-router-dom';
import AnimatedRoutes from './AnimatedRoutes';

export const StateContext = React.createContext();

function App() {

  const [state, dispatch] = useReducer(PercentReducer, {
    deposit: '',
    monthToCalculate: '',
    percentergPerYear: '',
    totalProfit: '',
    avgPerMonth: '',
    avgPerYear: '',
    addAmountToFill: '',
    chartData: {
      month: [],
      profit: [],
      balance: [],
      labels: [],
    },
    tableData: []
  });



  


  return (
  <StateContext.Provider value={{state: state, dispatch: dispatch}}>

<Router>

<AnimatedRoutes />

    </Router>
    </StateContext.Provider>
  );
}

export default App;
