import React, { useContext, useState } from 'react'
import { useEffect } from 'react';
import Table from './Table';
import { StateContext } from '../../App';
import { Header } from '../../Header';
import { ChartLines } from './ChartLines';
import { motion } from 'framer-motion';




function Percents() {

  let { state, dispatch } = useContext(StateContext)

  useEffect(() => {
    if (state.deposit && state.percentergPerYear && state.monthToCalculate) {

      dispatch({ type: 'CALCULATE_TABLE_DATA' })

    }
  }, [state.deposit, state.percentergPerYear, state.monthToCalculate])

  const fillAddAmount = (e) => {
    dispatch({ type: 'FILL_ADDAMOUNT_DATA', value: e.target.value, addAmountToFill: e.target.value })

  }

  const fillTableData = (num) => {
    dispatch({ type: 'FILL_TABLE_DATA', num: num })

  }

  const startDepoInputHandler = (e) => dispatch({ type: 'START_DEPOSIT_INPUT_VALUE', deposit: e.target.value })
  const percentsPerYearInputHandler = (e) => dispatch({ type: 'PERCENTAGE_PER_YEAR_INPUT', percentergPerYear: e.target.value })
  const monthToCalculateInputHandler = (e) => dispatch({ type: 'ADD_MONTH_TO_CALCULATE', monthToCalculate: e.target.value })

  return (<>
    <Header />




    <h1>Calculation of the compound interest rate</h1>
    <div className='info'>
      <motion.div
        initial={{ opacity: 0, x: -100 }}
        animate={{ opacity: 1, x: 0, transition: { delay: 0.3, duration: 1, type: 'spring' } }}
        exit={{ x: -100, opacity: 0, transition: { duration: 1 } }}
      >
        <div className='column'>
          <span>Deposit: <input type="number" placeholder='First depo' onChange={startDepoInputHandler} value={state.deposit} /></span>
          <span>Interest rate : <input type="number" placeholder='Bank % per year' onChange={percentsPerYearInputHandler} value={state.percentergPerYear} /></span>
          <span>Months to calculate: <input type="number" onKeyDown={(e) => e.key == 'Enter' && (monthToCalculateInputHandler(e), fillTableData(e.target.value), e.target.value = '')} placeholder={state.monthToCalculate} /></span>
        </div>
        <div className='column'>
          <span className='blue pad'>Average per month: {state.avgPerMonth}</span>
          <span className='red pad'>Average per year: {state.avgPerYear}</span>
          <span className='green pad'>Total profit: {state.totalProfit}</span>
        </div>
      </motion.div>
      <motion.div
        initial={{ opacity: 0, x: 600 }}
        animate={{ opacity: 1, x: -100, transition: { delay: 0.3, duration: 1, type: 'spring' } }}
        exit={{ x: 600, opacity: 0, transition: { duration: 1 } }}
        className='chart'>
        <ChartLines />
      </motion.div>
    </div>


    <motion.table
      initial={{ opacity: 0, y: 600 }}
      animate={{ opacity: 1, y: 0, transition: { delay: 0.5, duration: 1, type: 'spring' } }}
      exit={{ y: 600, opacity: 0, transition: { duration: 1 } }}
      className="table">

      <thead>
        <tr>
          <th scope="col">Month # </th>
          <th scope="col">Profit per month</th>
          <th scope="col">Current balance</th>
          <th scope="col">Add amount <input type="text" placeholder='fill all inputs' onChange={(e) => (fillAddAmount(e))} value={state.addAmountToFill} /></th>
        </tr>
      </thead>
      <tbody>
        <Table />
      </tbody>
    </motion.table>


  </>
  )
}


export default Percents;